		// 获取数据
		getData(){
			var arr = []
			for (let i = 0; i < this.tabBars.length; i++) {
				// 生成列表模板
				let obj = {
					// 1.上拉加载更多  2.加载中... 3.没有更多了
					loadmore:"上拉加载更多",
					list:[]
				}
				obj.list = demo
				arr.push(obj)
			}
			this.bookList = arr
		},
		// 监听滑动
		onChangeTab(e){
			this.changeTab(e.detail.current)
		},
		// 切换选项
		changeTab(index){
			if (this.tabIndex === index) {
				return;
			}
			this.tabIndex = index
			// 滚动到指定元素
			this.scrollInto = 'tab'+index
		},
		// 上拉加载更多
		loadmore(index){
			// 拿到当前列表
			let item = this.bookList[index]
			// 判断是否处于可加载状态（上拉加载更多）
			if (item.loadmore !== '上拉加载更多') return;
			// 修改当前列表加载状态
			item.loadmore = '加载中...'
			// 模拟数据请求
			setTimeout(()=>{
				// 加载数据
				item.list = [...item.list,...item.list]
				// 恢复加载状态
				item.loadmore = '上拉加载更多'
			},3000)
		}
	}
